.control {
  position: relative;
  cursor: pointer;
  padding-left: 26px;
  margin-top: 15px;
  max-width: 370px;

  @media (max-width: $max-tablet-width) {
    margin-top: 6px;
  }

  @media (max-width: $max-mobile-width) {
    padding-left: 30px;
    display: block;
    margin: 0 auto 0 0;
  }
}

.control:hover {
  opacity: 0.6;
}

.control:active {
  opacity: 1;
}

.control__mark-icon {
  display: none;
  position: absolute;
  top: 11px;
  left: 3px;
  width: 9px;
  height: 6px;
  content: "";
  z-index: 2;
  fill: transparent;
  top: 0;
  bottom: 0;
  left: 2px;
  margin: auto;
}

.control__mark {
  position: absolute;
  top: 7px;
  left: 0;
  width: 13px;
  height: 13px;
  border-radius: 2px;
  border: 1px solid $light-color-type-5;
  content: "";
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;

  @media (max-width: $max-mobile-width) {
    width: 12px;
    height: 12px;
  }
}

.control span:last-child {
  font-size: 14px;
  line-height: 15px;
  display: block;
}

.control input[type="checkbox"]:checked + .control__mark-icon {
  display: block;
}

.control input[type="checkbox"]:focus-visible ~ .control__mark {
  border: 1px solid $dark-color-type-7;
}
